				"html": "<p>This carousel was originally done for a bootstrap project. I quickly implemented it using <a href=\"http://jedrzejchalubek.com/glide/\">Glide JS</a> which fits in nicely with <a href=\"http://semantic-ui.com/\">Semantic UI</a> (the framework I use on this site). This post describes my implementation.</p>\n<p>Many people don't like carousels and I <em>somewhat</em> agree. Image carousels like the on above can be somewhat intruding to the content and are very often un-touched or swiped through by users. However, with a mobile first User Interface a carousel can be designed to act more as a swipe and can introduce an interesting way to present content.</p>\n<p>Good imagery with clean verbage can get a user to go through a carousel or can used to create a <em>side swipe</em> as demonstrated here. Notice how im my <em>Reference Rolex</em> a user can swipe or click from the side to view new content rather than to continue to scroll down. This, from my perspective, becomes most effective and relavant for users on a mobile view where swiping is more standard.</p>\n<p><strong>The CSS used</strong></p>\n<pre><code class=\"language-html\">&#x3C;link rel=\"stylesheet\" href=\"http://cdnjs.cloudflare.com/ajax/libs/semantic-ui/0.12.0/css/semantic.min.css\"/>\n</code></pre>\n<p><strong>The JS used</strong></p>\n<pre><code class=\"language-javascript\">&#x3C;script src=\"//code.jquery.com/jquery-1.11.0.min.js\">&#x3C;/script>\n&#x3C;script src=\"http://cdnjs.cloudflare.com/ajax/libs/semantic-ui/0.12.0/javascript&#x3C;ntic.min.js\">&#x3C;/script>\n&#x3C;script src=\"http://cdn.jsdelivr.net/jquery.glide/1.0.6/jquery.glide.min.js\">&#x3C;/script>\n</code></pre>\n<p><strong>The html</strong></p>\n<p>The HTML for the first 2 slides</p>\n<pre><code class=\"language-html\">&#x3C;div class=\"slider slider1\">\n  &#x3C;div class=\"slides\">\n    &#x3C;div class=\"slide-item item1\">\n    &#x3C;/div>\n    &#x3C;div class=\"slide-item item2\"> \n    &#x3C;/div>\n    &#x3C;div class=\"slide-item item3\">\n    &#x3C;/div>\n    &#x3C;div class=\"slide-item item4\">\n    &#x3C;/div>\n    &#x3C;/div>\n&#x3C;/div>\n</code></pre>\n<p><strong>The JS added locally</strong></p>\n<p>Just call glide to run in your local JS</p>\n<pre><code class=\"language-javascript\">$('.slider').glide();\n</code></pre>\n<p>Note: my JS for glide is slightly different but this is all you need.</p>\n<div class=\"code-sample\">\n\t<p styles=\"min-height: 300px;\" data-height=\"268\" data-theme-id=\"0\" data-slug-hash=\"71fdafe5e8fdae857086e9be2bd928ba\" data-default-tab=\"result\" data-user=\"yowainwright\" class='codepen'>See the Pen <a href='http://codepen.io/yowainwright/pen/71fdafe5e8fdae857086e9be2bd928ba/'>71fdafe5e8fdae857086e9be2bd928ba</a> by Jeff Wainwright (<a href='http://codepen.io/yowainwright'>@yowainwright</a>) on <a href='http://codepen.io'>CodePen</a>.</p>\n\t<script async src=\"//assets.codepen.io/assets/embed/ei.js\"></script>\n</div>",
